
body {
    font-size: 1rem;
    font-family: Arial, sans-serif;
}

.ListItem {
    border: 1px solid #bfbfbf;
    margin-bottom: 1rem;
    display: -ms-flexbox;
    display: flex;
}

.checkboxWrapper {
    min-height: 4rem;
    background-color: #d9d9d9;
    width: 4rem;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;

}

.cb {
    -ms-flex-item-align: center;
        align-self: center;
    max-width: 1rem;
}

.userDataWrapper {
    -ms-flex-align: stretch;
        align-items: stretch;
    width: 100%;
}

.name {
    background: #e9e9e9;
    padding: 0.5rem;
}

.email {
    padding: 0.5rem;
}

.btn {
    display: inline-block;
    font-weight: 400;
    line-height: 1.25;
    text-align: center;
    white-space: nowrap;
    vertical-align: middle;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    border: 1px solid transparent;
    padding: .5rem 1rem;
    font-size: 1rem;

}

.btn-primary {
    background-color: #76cdd3;
}


